*{
  padding: 5px;
}

body{
  background-color: rgb(58, 57, 57);
  color: white;
}

.nav_bar{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.getBtn{
  border: 2px solid black;
  border-radius: 4px;
  padding: 10px;
  color: white;
  font-weight: bold;
  font-size: 1rem;
  font-variant-caps:normal;
  background-color: rgb(228, 55, 25);
}
.getBtn:hover{
  background-color: rgb(214, 127, 95);
}
.loader {
  position: absolute;
  top:50%;
  left: 50%;
  visibility: hidden;
  border: 10px solid #0090b4; /* Light grey */
  border-top: 10px solid #db3934; /* Blue */
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.allUsers{
    margin-left: 20%;
  margin-right:20%;
}
.userBox{
  position: relative;
  display: flex;
  background-color: white;
  width: 95%;
  color: black;
  padding: 10px;
  border-radius: 6px;
  margin: 10px;
}
.userImage{
  border-radius: 50%;
}
.content{
  margin: 3px;
  color: darkblue;
  font-size: 20px;
}
